import React from "react";
// import "slick-carousel/slick/slick.css";
// import "slick-carousel/slick/slick-theme.css";
import '../assets/css/slideshow.css'
import Carousel, { slidesToShowPlugin, arrowsPlugin } from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';
import Slide1 from "../assets/images/slide1.jpg";
import Slide2 from "../assets/images/slide2.jpg";
import Slide3 from "../assets/images/slide3.jpg";
import LeftArrow from "../assets/images/arrow_left.png";
import RightArrow from "../assets/images/arrow_right.png";
// import Slider from "react-slick";
import * as mainStyles from '../assets/css/main.module.css';


const Main = () => {

    const mobilePlugins = [
        'arrows',
        'infinite',
        {
            resolve: arrowsPlugin,
            options: {
            arrowLeft: <img src={LeftArrow} alt="Previous picture"/>,
            arrowLeftDisabled:<img src={LeftArrow}/>,
            arrowRight: <img src={RightArrow}/>,
            arrowRightDisabled: <img src={RightArrow} alt="Next picture"/>,
            addArrowClickHandler: true,
            }
        }
    ]

    const tabletPlugins = [
        'centered',
        'arrows',
        'infinite',
        {
            resolve: slidesToShowPlugin,
            options: {
                numberOfSlides: 1.1,
            }
        },
        {
            resolve: arrowsPlugin,
            options: {
            arrowLeft: <img src={LeftArrow} alt="Previous picture"/>,
            arrowLeftDisabled:<img src={LeftArrow}/>,
            arrowRight: <img src={RightArrow}/>,
            arrowRightDisabled: <img src={RightArrow} alt="Next picture"/>,
            addArrowClickHandler: true,
            }
        }
    ]

    const desktopPlugins = [
        'centered',
        'arrows',
        'infinite',
        {
            resolve: slidesToShowPlugin,
            options: {
                numberOfSlides: 1.2,
            }
        },
        {
            resolve: arrowsPlugin,
            options: {
            arrowLeft: <img src={LeftArrow} alt="Previous picture"/>,
            arrowLeftDisabled:<img src={LeftArrow}/>,
            arrowRight: <img src={RightArrow}/>,
            arrowRightDisabled: <img src={RightArrow} alt="Next picture"/>,
            addArrowClickHandler: true,
            }
        }
    ]

    return(
        <div className={mainStyles.container}>
            <Carousel className={mainStyles.carousel}
                plugins={[
                    'centered',
                    'arrows',
                    'infinite',
                    {
                        resolve: slidesToShowPlugin,
                        options: {
                            numberOfSlides: 1.4,
                        }
                    },
                    {
                        resolve: arrowsPlugin,
                        options: {
                        arrowLeft: <img src={LeftArrow} alt="Previous picture"/>,
                        arrowLeftDisabled:<img src={LeftArrow}/>,
                        arrowRight: <img src={RightArrow}/>,
                        arrowRightDisabled: <img src={RightArrow} alt="Next picture"/>,
                        addArrowClickHandler: true,
                        }
                    }
                ]}
                breakpoints={{
                    1824: {
                        plugins: desktopPlugins
                    },
                    1024: {
                        plugins: tabletPlugins
                    },
                    480: {
                        plugins: mobilePlugins
                    }
                }}
            >
                <div className={mainStyles.imagecontainer}>
                    <img src={Slide1} alt="slide 1"/>
                </div>
                <div className={mainStyles.imagecontainer}>
                    <img src={Slide2} alt="slide 2"/>
                </div>
                <div className={mainStyles.imagecontainer}>
                    <img src={Slide3} alt="slide 3"/>
                </div>
            </Carousel>
        </div>

    );
};

export default Main;